import { listDevices, connect } from "../../services/adb";

const adbModule = {
  namespaced: true,
  state: {
    devices: [],
  },
  mutations: {
    setDevices(state, payload) {
      state.devices = payload.devices;
    },
  },
  actions: {
    fetchDevices(state) {
      return new Promise((res, rej) => {
        listDevices()
          .then(d => state.commit("setDevices", d.data))
          .then(() => res(1))
          .catch(err => rej(err));
      })
    },
    connectDevice(state, payload) {
      const [host, port] = payload.split(":");
      return new Promise((res, rej) => {
        connect(host, port)
          .then(d => {
            res(d);
          })
          .catch(err => rej(err));
      })
    }
  }
}

export default adbModule;