<template>
  <v-dialog :value="show" persistent max-width="540px">
    <v-card>
      <v-card-title> Connect your ADB Client </v-card-title>
      <v-card-text>
        <v-alert v-if="alert" type="error" outlined text dense>
          <span>Error connecting to ADB.</span>
        </v-alert>
        <v-text-field
          v-model="hostAddress"
          label="Host Address"
          placeholder="127.0.0.1:5555"
          :rules="hostRules"
          required
        >
        </v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" :loading="loading" @click="connectAdb">
          Connect
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "AdbDialog",
  props: {
    show: Boolean,
  },
  data: () => ({
    loading: false,
    alert: false,
    hostAddress: "",
    hostRules: [
      (v) => !!v || "Host address must not be empty",
      (v) =>
        /(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}:\d+/.test(
          v
        ) || "Invalid host address",
    ],
  }),
  methods: {
    connectAdb() {
      this.loading = true;
      this.$store
        .dispatch("adb/connectDevice", this.hostAddress)
        .then((res) => {
          if (res.data.connect) {
            this.$store.dispatch("adb/fetchDevices");
          } else {
            this.alert = true;
          }
        })
        .catch(() => {
          this.alert = true;
          this.loading = false;
        });
    },
  },
};
</script>
