import { getRecentTask } from "../../services/farmer";

const recentTaskModule = {
  namespaced: true,
  state: {
    stages: [],
  },
  mutations: {
    setStages(state, payload) {
      state.stages = payload;
    }
  },
  actions: {
    fetchStages(state) {
      getRecentTask().then((res) => {
        state.commit("setStages", res.data);
      });
    }
  }
}

export default recentTaskModule;