const initialState = {
  onProgress: "",
  log: [],
  completed: [],
  started: false,
}

const gameStateModule = {
  namespaced: true,
  state: {
    ...initialState
  },
  mutations: {
    start(state) {
      state.started = true;
    },
    stop(state) {
      state.started = false;
    },
    setGameState(state, payload) {
      state.onProgress = payload.on_progress;
      state.log = payload.log;
      state.completed = payload.completed;
    },
    addLog(state, payload) {
      state.log = [...state.log, payload];
    },
    reset(state) {
      const s = initialState;
      Object.keys(s).forEach(k => {
        state[k] = s[k];
      });
    }
  }
}

export default gameStateModule;