<template>
  <v-app>
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
    <navbar></navbar>
    <v-content style="overflow: hidden">
      <v-container class="mb-12" fluid>
        <transition name="fade-transition" mode="out-in">
          <router-view :ws="ws" @stop="stopFarmer"></router-view>
        </transition>
      </v-container>
      <footer-ver> </footer-ver>
    </v-content>
    <err-dialog :show="dialog.show" :type="dialog.type" :msg="dialog.msg">
    </err-dialog>
    <snackbar
      :show="snackbar.show"
      :type="snackbar.type"
      :msg="snackbar.msg"
      @close="snackbar.show = false"
    ></snackbar>
    <adb-dialog :show="!adbOk && !loading"> </adb-dialog>
  </v-app>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import ErrDialog from "./components/ErrDialog.vue";
import Snackbar from "./components/Snackbar.vue";
import FooterVer from "./components/FooterVer.vue";
import AdbDialog from "./components/AdbDialog.vue";
export default {
  created() {
    this.init();
    this.initWS();
  },
  name: "App",
  components: {
    Navbar,
    ErrDialog,
    Snackbar,
    FooterVer,
    AdbDialog,
  },
  data: () => ({
    loading: true,
    dialog: {
      show: false,
      type: "",
      msg: "",
    },
    snackbar: {
      show: false,
      type: "",
      msg: "",
    },
    ws: null,
    startMsg: "Farmer has succesfully started.",
  }),
  computed: {
    adbOk() {
      return this.$store.state.adb.devices.length >= 1;
    }
  },
  methods: {
    init() {
      this.$store.dispatch("recent/fetchStages");
      this.$store
        .dispatch("adb/fetchDevices")
        .then(() => (this.loading = false));
    },
    showDialog(type, msg) {
      this.dialog.show = true;
      this.dialog.type = type;
      this.dialog.msg = msg;
    },
    showSnackbar(type, msg) {
      this.snackbar.show = true;
      this.snackbar.type = type;
      this.snackbar.msg = msg;
    },
    initWS() {
      this.ws = new WebSocket("ws://localhost:5000/ws");
      this.ws.addEventListener("message", (e) => {
        const { event, msg } = JSON.parse(e.data);
        console.log(event, msg);
        if (event === "req-current-state") {
          this.$store.commit("gamestate/setGameState", msg);
        }
        if (event === "on-log") {
          this.$store.commit("gamestate/addLog", msg);
        }
        if (event === "on-start") {
          this.$router.push("/manager");
          this.$store.commit("gamestate/start");
          this.showSnackbar("success", this.startMsg);
        }
        if (event === "on-exit") {
          console.log("exit");
          this.$store.commit("gamestate/stop");
          this.$store.dispatch("recent/fetchStages");
        }
      });
      this.ws.addEventListener("close", () => console.log("ws closed"));
    },
    stopFarmer() {
      this.ws.send(JSON.stringify({ event: "on-stop", msg: "" }));
      this.$store.commit("gamestate/stop");
    },
  },
};
</script>
