import axios from "./axios";

const start = (stages, refill) => axios.post(`/farmer/start?refill=${refill}`, stages);
const startOneStage = (amount, refill) => axios.post(`/farmer/start-onestage?amount=${amount}&refill=${refill}`);
const getRecentTask = () => axios.get("/farmer/get-recent-task");


export { start, startOneStage, getRecentTask };
