<template>
  <v-snackbar :value="show" :color="type" timeout="2000">
    {{ msg }}
    <template v-slot:action="{ attrs }">
      <v-btn color="dark" text v-bind="attrs" @click="$emit('close')">
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
export default {
  name: "Snackbar",
  props: {
    show: Boolean,
    type: String,
    msg: String,
  },
};
</script>